import Post from '../Post/Post'
import posts from '../../db.json'
import './Wall.css'
import React from 'react'

const Wall = () => {
    posts.posts.sort(function(a, b) {
        return a.votes - b.votes
    })
    posts.posts.reverse()
    console.log(posts)
    return (
        <>
            {
            posts.posts.map(post => (
                <Post id={post.id}/>
            ))
            }
        </>
    )
}

export default Wall
